@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #112240;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.vl {
  border-left: 2px solid #04AA6D;
  height: 200px;
  align-self: center;
}

.vl-r {
  border-left: 2px solid #04AA6D;
  height: 200px;
  align-self: flex-end;
}

.BottomLine {
  border-bottom: 2px solid #04AA6D;
}

.nav {
    padding-top: 0.5rem;
    width: 100%;
    display: flex;
    height: 4rem;
    position: fixed;
    background-color: #112240;
    z-index: 2;
    box-shadow: 0 0 3px black;
}

.logo {
    color: #64ffda;
    font-size: 2rem;
    padding-left: 4rem;
}


.NavButtonsContainer {
    display: flex;
    width: 35rem;
    justify-content: space-evenly;
    margin-right: 4rem;
    margin-left: auto;
}

.NavButton {
    padding: 10px;
    text-decoration: none;
    color: #64ffda;
}

.NavButton:hover {
    cursor: pointer;
}


.Main {
    width: 100%;
    margin-top: 5rem;
}

.MainName {
    font-size: 8rem;
}

.TextSnippet {
    padding-bottom: 5rem;
    color: #ccd6f6;
}

.Divider {
    width: 30%;
    background-color: #04AA6D;
    border: none;
    height: 1px;
}

.focus-in-expand {
	-webkit-animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) 1s both;
	        animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) 1s both;
}

@-webkit-keyframes focus-in-expand {
    0% {
      letter-spacing: -0.5em;
      -webkit-filter: blur(12px);
              filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }
  @keyframes focus-in-expand {
    0% {
      letter-spacing: -0.5em;
      -webkit-filter: blur(12px);
              filter: blur(12px);
      opacity: 0;
    }
    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
      opacity: 1;
    }
  }
  
.Section {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 25rem;
    padding-left: 4rem;
}

.Container {
    width: 55rem;
    display: flex;
    flex-wrap: wrap;
    min-height: 30rem;
}

.BioTextContainer {
    display: grid;
    /* grid-template-rows: 25% 75%; */
    padding: 1rem;
    width: 25rem;
    font-size: 14px;
    min-height: 30rem;
}

.BioTextBody {
    text-align: start;
}

.BioImageContainer {
    display: flex;
    border-bottom: 2px solid #04AA6D;
    width: 25rem;
    align-items: center;
    justify-content: center;
}

.Title {
    color: #64ffda;
}

@media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }

.ExperienceLeft {
    width: 25rem;
     display: flex;
     flex-wrap: wrap;
}

.ExperienceTopLeft {
    display: flex;
    align-items: center;
}


.ExperienceText {
    padding-top: 2rem;
    padding-right: 3rem;
    padding-left: 2rem;
    width: 23rem;
    font-size: 13px;
}

.JobTitle {
    color: #ccd6f6;
}

.Description {
    text-align: start;
}

.LanguagesContainer {
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.LanguageCard {
    display: grid;
    grid-template-rows: 4rem 1fr;
}

.LanguageTitle {
    grid-row-start: 1;
}

.LanguageImage {
    grid-row-start: 2;
}
.Project {
    width: 100%;
}

.ProjectContainer {
    margin-bottom: 10rem;
}

.ProjectLeft {
    width: 24rem;
    display: grid;
    grid-template-columns: 1rem 1fr;
    border-top: 2px solid #04AA6D;
}

.ProjectTitle {
    width: 24rem;
    color: #64ffda;
}

.ProjectText{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    color: #ccd6f6;
}

.projectvl {
    display: flex;
    align-content: flex-start;
}

.ProjectRight {
    width: 24rem;
    display: flex;
    align-content: center;
    justify-content: center;
}

.ProjectImage {
    display: flex;
    align-items: center;
}
.SocialsContainer {
    position: fixed;
    height: 100vh;
    width: 4rem;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-end;
    justify-content: center;
}

.Socials {
    width: 4rem;
    margin: 1rem;
}

.vlContainer {
    margin-top: 10px;
    width: 4rem;
    display: flex;
    justify-content: center;
}

.socialvl {
    border-left-color: #ccd6f6;
    height: 5rem;
}

.longersocialvl{
    border-left-color: #ccd6f6;
    height: 10rem;
}
.ModifiedSection {
    width: 100%;
    margin-bottom: 10rem;
    display: flex;
    justify-content: center;
}

.FormContainer {
    height: 25rem;
    width: 25rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: space-around;
    border: 1px solid #04AA6D;;
}

input {
    width: 14.8rem;
    /* background-color: #ccd6f6; */
}

textarea {
    width: 14.8rem;
    /* background-color: #ccd6f6; */
}

.inputs {
    width: 20rem;
    height: 2rem;
}

.message {
    height: 8rem;
    margin-bottom: 1rem;
}

.submitButton {
    width: 10rem;
    background-color: #ccd6f6;
    border: 1px solid #ccd6f6;
    color:#112240;
}
.Footer {
    height: 5rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
}
